<template>
  <div class="welcome-container">
    <div class="animation-wrapper">
      <div class="text-container">
        <h1>
          {{ displayedText }}
          <span class="cursor"></span>
        </h1>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WelcomeAnimation',
  data() {
    return {
      welcomeTexts: [
        '欢迎来到 科技引领时代 探索无限可能 这里是展示创意鱼技术的平台。',
        '深夜下班 看看今天的世界又发生了什么吧？！',
        '人总要进步 科技也是',
        '懒 也是在进步 科技 也是懒的一部分',
        '创新改变世界 科技点亮未来',
        '每一天都是新的开始 每一次思考都是进步',
        '探索未知 创造可能'
      ],
      displayedText: '',
      currentTextIndex: 0,
      currentCharIndex: 0,
      isDeleting: false,
      // 新的打字机参数设置
      typeSpeed: 80,      // 打字速度（加快）
      deleteSpeed: 100,    // 删除速度
      pauseAfterType: 2000,// 打字完成后暂停时间（缩短）
      pauseAfterDelete: 500,// 删除完成后暂停时间
      // 动画控制变量
      animationId: null,
      lastTime: 0,
      // 随机化打字速度，增加真实感
      randomSpeed: true,
      minSpeedVariation: 0.7,  // 最小速度系数
      maxSpeedVariation: 1.3   // 最大速度系数
    }
  },
  mounted() {
    // 延迟启动，让页面完全加载
    setTimeout(() => {
      this.typeWriterLoop()
    }, 300)
  },
  beforeUnmount() {
    // 清理动画帧
    if (this.animationId) {
      cancelAnimationFrame(this.animationId)
    }
  },
  methods: {
    // 使用requestAnimationFrame实现的打字机动画循环
    typeWriterLoop(timestamp = 0) {
      if (!this.lastTime) {
        this.lastTime = timestamp
      }
      
      const currentText = this.welcomeTexts[this.currentTextIndex]
      const speedFactor = this.randomSpeed ? 
        (Math.random() * (this.maxSpeedVariation - this.minSpeedVariation) + this.minSpeedVariation) : 1
      
      // 计算当前应该使用的时间间隔
      const currentSpeed = this.isDeleting ? 
        (this.deleteSpeed / speedFactor) : 
        (this.typeSpeed / speedFactor)
      
      // 检查是否应该执行下一步操作
      if (timestamp - this.lastTime > currentSpeed) {
        if (this.isDeleting) {
          // 删除模式
          this.displayedText = currentText.substring(0, this.currentCharIndex - 1)
          this.currentCharIndex--
          
          if (this.currentCharIndex === 0) {
            // 删除完成，切换到下一段文本
            this.isDeleting = false
            this.currentTextIndex = (this.currentTextIndex + 1) % this.welcomeTexts.length
            
            // 暂停一段时间后再开始下一段文本
            setTimeout(() => {
              this.lastTime = 0
              this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
            }, this.pauseAfterDelete)
            return
          }
        } else {
          // 打字模式
          this.displayedText = currentText.substring(0, this.currentCharIndex + 1)
          this.currentCharIndex++
          
          if (this.currentCharIndex === currentText.length) {
            // 打字完成，准备删除
            this.isDeleting = true
            
            // 暂停一段时间后开始删除
            setTimeout(() => {
              this.lastTime = 0
              this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
            }, this.pauseAfterType)
            return
          }
        }
        
        this.lastTime = timestamp
      }
      
      // 继续动画循环
      this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
    },
    
    // 添加一个方法来重置打字机效果（如果需要）
    resetTypewriter() {
      if (this.animationId) {
        cancelAnimationFrame(this.animationId)
      }
      this.displayedText = ''
      this.currentTextIndex = 0
      this.currentCharIndex = 0
      this.isDeleting = false
      this.lastTime = 0
      this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
    }
  }
}
</script>

