  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
  }

  /* 使用CSS动画实现光标闪烁 */
.cursor {
  display: inline-block;
  margin-left: 4px;
  background-color: #333;
  width: 3px;
  height: 1.2em;
  vertical-align: middle;
  animation: cursorBlink 1s infinite;
}

@keyframes cursorBlink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

/* 响应式设计 */
@media (max-width: 1024px) {
  .text-container h1 {
